import React from 'react'
import Navbar from '../Navbar'
import Footer from '../Footer'
import Content from './Content'
import Form from './Form'
import { CartProvider } from 'react-use-cart'

function AllContact() {
  return (
    <CartProvider>
        <Navbar/>
        <Content/>
        <Form/>
        <Footer/>
    </CartProvider>
  )
}

export default AllContact;