import React from 'react'
import { NavLink } from 'react-router-dom'

function Ingredients() {
    return (
        <div>
            {/* <!-- Start ingredients section --> */}
            <div className="ingredients-content container-fluid  wow bounceInRight ">
                <div className="row">
                    <div className="col-md-6 ingredients-content-left">
                        <h2>Only the best ingredients</h2>
                        <p>We use only natural ingredients to prepare our pizzas, most of which we import from Italy.
                            Italian burrata, spices, olive oil? These and many other additives determine the taste of our
                            products.
                        </p>
                        <NavLink className="btn btn-outline-light navbar-button" to="/shop">Order online</NavLink>
                    </div>
                    <div className="col-md-6 ingredients-content-right">
                        <img src="img/pizza.png" />
                    </div>
                </div>
            </div>
            {/* <!-- End ingredients section --> */}
        </div>
    )
}

export default Ingredients