import React from 'react'
import { NavLink } from 'react-router-dom'

function Pizzeria() {
  return (
    <div>
      {/* <!-- Start about pizzeria section --> */}
      <div className="about-pizzeria">
        <h2 className="wow flipInX">Discover what authentic pizza tastes like</h2>
        <NavLink className="btn btn-outline-warning py-3 px-5" to="/shop">Order online</NavLink>
      </div>
      {/* <!-- End about pizzeria section --> */}
    </div>
  )
}

export default Pizzeria