import React from 'react'
import { NavLink } from 'react-router-dom'

function Sunny() {
    return (
        <div>
            {/* <!-- Start about sunny section --> */}
            <div className="container-fluid bg-warning wow bounceInLeft">
                <div className="row">
                    <div className="col-md-6 sunny-left">
                        <h3>Welcome to sunny Italy!</h3>
                        <p>If you choose our Italian pizzeria, you certainly won't regret it. We make our pizzas with the
                            highest quality products, imported straight from Italy.
                            <br /><br /> We invite you on a journey to sunny Italy. Our adventure with pizza began during one of our
                            family trips. We fell in love with regional flavors and have longed for them every day ever since.
                            By creating EXPRESS PIZZA ITALY we want you to have a similar experience.
                        </p>
                        <NavLink className="btn btn-danger" to="/shop">Order online</NavLink>
                    </div>
                    <div className="col-md-6 sunny-right">
                        <img src="img/pizza 2.png" alt="" />
                    </div>
                </div>
            </div>
            {/* <!-- End about sunny section --> */}
        </div>
    )
}

export default Sunny