import React from 'react'

function Pizza() {
    return (
        <div>
            {/* <!-- Start about pizza section --> */}
            <div className="container about-pizza boundry wow bounceInRight">
                <div className="row">
                    <div className="col-md-4 boundry">
                        <h3>Thin crust dough</h3>
                        <p>The dough of an Italian pizza should be thin, and the edges should be fluffy in the middle and crispy
                            on the outside.</p>
                    </div>
                    <div className="col-md-4">
                        <img src="img/pizza hand.jpg" />
                    </div>
                    <div className="col-md-4 boundry">
                        <h3>Excellent toppings</h3>
                        <p>The ingredients determine the pizza taste  you will find only the best seasonal ingredients at our
                            place.</p>
                    </div>
                </div>
            </div>
            {/* <!-- End about pizza section --> */}
        </div>
    )
}

export default Pizza