import React from 'react'
import Navbar from '../Navbar';
import Footer from '../Footer';
import { CartProvider } from 'react-use-cart';

function Happy() {
    return (
        <CartProvider>
            <Navbar />
            <div className="happy">
                <h3 className="Happy animate__animated animate__rubberBand">Happy Meal</h3>
                <div className="smile">
                    <i className="fa-regular fa-face-smile-beam animate__animated animate__flip"></i>
                </div>
            </div>
        </CartProvider>
    )
}

export default Happy;