import React from 'react'
import { NavLink } from 'react-router-dom'

function Footer() {
    return (
        <div>
            {/* <!-- Start Footer section --> */}
            <div className="container-fluid footer">
                <div className="row">
                    <div className="col-md-12 footer-head">
                        <img src="img/png.png" width="100" height="17" className="img-responsive inline m-b-md"
                            alt="Express Pizza Italy" />
                    </div>
                    <div className="col-md-12"></div>
                    <div className="col-md-8 footer-content m-auto">
                        <NavLink aria-current="page" to="/">Home</NavLink>
                        <NavLink to="/about">About us</NavLink>
                        <NavLink to="/contact">Contact</NavLink>
                        <NavLink to="/shop">Order online</NavLink>
                        <NavLink to="/">Unsubscribe</NavLink>
                    </div>
                    <div className="col-md-12"></div>
                    <div className="col-md-8 footer-details m-auto">
                        <NavLink to="/contact"><i className="fa-solid fa-phone"></i> +1 123123123</NavLink>
                        <NavLink to="/contact"><i className="fa-solid fa-envelope"></i> contact@upmenu.com</NavLink>
                    </div>
                    <div className="col-md-12"></div>
                    <div className="col-md-4 m-auto">
                        <h5>Online Food Ordering System by</h5>
                    </div>
                    <div className="col-md-12"></div>
                    <div className="col-md-3 footer-final m-auto">
                        <NavLink to="/menu">Upmenu</NavLink>
                    </div>
                </div>
            </div>
            {/* <!-- End Footer section --> */}
        </div>
    )
}

export default Footer