import React from 'react'
import Navbar from '../Navbar';
import Api from './Api'
import { CartProvider } from 'react-use-cart';
function AllMenu() {
  return (
    <CartProvider>
      <Navbar />
      <section className="shop_section layout_padding">
        <div className="container">
          <div className="heading_container heading_center">
            <h2>Meals</h2>
          </div>
          <div className="row">
            {Api.map((item) =>
              <div className="col-sm-6 col-md-6 col-lg-4 wow swing center ">
                <div className="box">
                  <div className="img-box">
                    <img src={item.image} alt="" />
                  </div>
                  <div className="detail-box">
                    <h5 id="text-menu-color"> {item.name}</h5>
                  </div>
                  <div className="detail-box">
                    <h6>Price:<span> {item.price}$</span></h6>
                    <h6>Rating:<span> {item.rating}<i class="fa-solid fa-star" style={{color: "#FFD43B"}}></i></span></h6>
                  </div>
                  <div className="detail-box">
                    <h5 id="text-menu-color"> {item.mealType}</h5>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
      </section>
    </CartProvider>
  ) 
}

export default AllMenu;