import React from 'react';
import Navbar from '../Navbar';
import Footer from '../Footer';
import { CartProvider } from 'react-use-cart';

function AllRegister() {

  // The register function that validates the form using direct DOM manipulation
  function register(e) {
    e.preventDefault(); // Prevent default form submission behavior

    // Get form values using document.querySelector()
    const first = document.querySelector(".first-form").value;
    const last = document.querySelector(".last-form").value;
    const phone = document.querySelector(".phone-form").value;
    const email = document.querySelector(".email-form").value;
    const pass = document.querySelector(".pass-sign").value;
    const confirmPass = document.querySelector(".confirm-pass-sign").value;
    const error = document.querySelector(".error");

    // Validation logic
    if (first === "" && last === "" && phone === "" && email === "") {
        error.innerHTML = `<i class="fa-solid fa-circle-exclamation" style="color: #ff0000;"></i> Write Your Data in Form`;
        return false;
      } else if (first === "") {
        error.innerHTML = `<i class="fa-solid fa-circle-exclamation" style="color: #ff0000;"></i> Write Your First Name`;
        return false;
      } else if (isNaN(first)===0) {
        error.innerHTML = `<i class="fa-solid fa-circle-exclamation" style="color: #ff0000;"></i> Enter Your First Name correctly please`;
        return false;
      } else if (first.length > 15 || first.length < 4) {
        error.innerHTML = `<i class="fa-solid fa-circle-exclamation" style="color: #ff0000;"></i> First Name should be between 4 and 15 characters`;
        return false;
      } 
      else if (last === "") {
          error.innerHTML = `<i class="fa-solid fa-circle-exclamation" style="color: #ff0000;"></i> Write Your Last Name`;
          return false;
        } else if (isNaN(last)===0) {
            error.innerHTML = `<i class="fa-solid fa-circle-exclamation" style="color: #ff0000;"></i> Enter Your Last Name correctly please`;
            return false;
        } else if (last.length > 15 || last.length < 4) {
            error.innerHTML = `<i class="fa-solid fa-circle-exclamation" style="color: #ff0000;"></i> Last Name should be between 4 and 15 characters`;
            return false;
        } 
        else if (phone === "") {
          error.innerHTML = `<i class="fa-solid fa-circle-exclamation" style="color: #ff0000;"></i> Write Your Phone Number`;
          return false;
        } else if (isNaN(phone)) {
          error.innerHTML = `<i class="fa-solid fa-circle-exclamation" style="color: #ff0000;"></i> Enter Your Phone Number correctly`;
          return false;
        } else if (phone.length < 11) {
            error.innerHTML = `<i class="fa-solid fa-circle-exclamation" style="color: #ff0000;"></i> Phone number must be at least 11 digits`;
            return false;
        } 
      else if (email === "") {
        error.innerHTML = `<i class="fa-solid fa-circle-exclamation" style="color: #ff0000;"></i> Write Your Email`;
        return false;
      } else if (isNaN(email)===0) {
        error.innerHTML = `<i class="fa-solid fa-circle-exclamation" style="color: #ff0000;"></i> Enter a valid email address`;
        return false;
      } else if (email.indexOf("@") === -1) {
          error.innerHTML = `<i class="fa-solid fa-circle-exclamation" style="color: #ff0000;"></i> Enter Valid Email correctly please`;
          return false;
      } else if (pass === "") {
        error.innerHTML = `<i class="fa-solid fa-circle-exclamation" style="color: #ff0000;"></i> Enter Your Password`;
        return false;
      } else if (confirmPass === "") {
        error.innerHTML = `<i class="fa-solid fa-circle-exclamation" style="color: #ff0000;"></i> Confirm Enter Your Password`;
        return false;
      } 
       else if (confirmPass !== pass) {
        error.innerHTML = `<i class="fa-solid fa-circle-exclamation" style="color: #ff0000;"></i> Repeat Your Password Correctly`;
        return false;
      } 
      else{

          return true;  // If everything is correct, allow form submission
      }
  }

  return (
    <CartProvider>
      {/* Register content section */}
      <div className="container-fluid contact-form">
        <div className="row">
          <div className="col-md-6 contact-form-left wow bounceInLeft">
            <h4>Register</h4>
            <h2>Order an Italian pizza!</h2>
            <p>Use our form and order a pizza with delivery to your home. We guarantee it will be delivered to you warm and in <span>less</span> than <span>half an hour.</span></p>
          </div>
          <div className="col-md-6 contact-form-right wow bounceInRight">
            {/* Form with onSubmit that calls the register function */}
            <form
              className="contact-form-right-form"
              onSubmit={(e) => {
                if (register(e)) {
                  // If validation is successful, further actions can be performed
                  window.location.href = '/welcome';
                  // For example, you can make an API call here to submit the form
                }
              }}
            >
              {/* Error message will be displayed here */}
              <h2 className="error" style={{ color: '#ff0000' }}></h2>

              <label>
                <i className="fa-solid fa-circle" style={{ color: '#ff0a0a' }}></i> First Name:
              </label>
              <input
                type="text"
                className="form-control first-form"
                name="first"
                placeholder="Enter First Name"
              />

              <label>
                <i className="fa-solid fa-circle" style={{ color: '#ff0a0a' }}></i> Last Name:
              </label>
              <input
                type="text"
                className="form-control last-form"
                name="last"
                placeholder="Enter Last Name"
              />

              <label>
                <i className="fa-solid fa-phone" style={{ color: 'red' }}></i> Phone:
              </label>
              <input
                type="text"
                className="form-control phone-form"
                name="phone"
                placeholder="Enter Phone"
              />

              <label>
                <i className="fa-solid fa-envelope" style={{ color: 'red' }}></i> E-mail:
              </label>
              <input
                type="text"
                className="form-control email-form"
                name="email"
                placeholder="Enter E-mail"
              />

              <label>
                <i className="fa-solid fa-key" style={{ color: 'red' }}></i> Password:
              </label>
              <input
                type="password"
                className="form-control pass-sign"
                name="password"
                placeholder="Enter Password"
              />

              <label>
                <i className="fa-solid fa-key" style={{ color: 'red' }}></i> Confirm Password:
              </label>
              <input
                type="password"
                className="form-control confirm-pass-sign"
                name="confirmPassword"
                placeholder="Confirm Password"
              />

              <label>
                <i className="fa-solid fa-comment" style={{ color: 'red' }}></i> Your Comment:
              </label>
              <textarea
                className="form-control"
                name="comment"
                placeholder="Enter Comment"
              ></textarea>

              <input
                type="submit"
                className="btn-danger submit form-control"
                value="Register"
              />
            </form>
          </div>
        </div>
      </div>
    </CartProvider>
  );
}

export default AllRegister;
