import React from "react";
import App from "./App";
import {
    createBrowserRouter,
    RouterProvider,
} from "react-router-dom";
import { createRoot } from "react-dom/client";
import AllAbout from "./About/AllAbout";
import AllContact from "./Contact/AllContact";
import AllHappy from "./Happy/AllHappy"
import AllWelcome from "./Welcome/AllWelcome";
import AllSign from "./Signin/AllSign";
import AllRegister from "./Register/AllRegister";
import AllMenu from "./Menu/AllMenu";
import AllShop from "./Shop/AllShop"
import AllCart from "./Cart/AllCart";
import ScrollToTop from "react-scroll-to-top";
const router = createBrowserRouter([
    {
        path: "/",
        element: <App />,
    },
    {
        path: "about",
        element: <AllAbout />,
    },
    {
        path: "contact",
        element: <AllContact />,
    },
    {
        path: "happy",
        element: <AllHappy/>,
    },
    {
        path: "welcome",
        element: <AllWelcome/>,
    },
    {
        path: "sign",
        element: <AllSign/>,
    },
    {
        path: "register",
        element: <AllRegister/>,
    },
    {
        path: "menu",
        element: <AllMenu/>,
    },
    {
        path: "shop",
        element: <AllShop/>,
    },
    {
        path: "cart",
        element: <AllCart/>,
    },
]);

createRoot(document.getElementById("root")).render(
    <div>
    <RouterProvider
        router={router}
    />
    <ScrollToTop
    smooth
    viewBox="0 0 384 512"
    svgPath="M214.6 41.4c-12.5-12.5-32.8-12.5-45.3 0l-160 160c-12.5 12.5-12.5 32.8 0 45.3s32.8 12.5 45.3 0L160 141.2 160 448c0 17.7 14.3 32 32 32s32-14.3 32-32l0-306.7L329.4 246.6c12.5 12.5 32.8 12.5 45.3 0s12.5-32.8 0-45.3l-160-160z"
    color="red"
    />
    </div>
);
