import React from 'react'
import { NavLink } from 'react-router-dom';
import { CartProvider, useCart } from 'react-use-cart';
import { useState } from "react";
import {
    Collapse,
    NavbarToggler,
} from "reactstrap";
function Navbar() {
    const { totalItems } = useCart()
    const [isOpen, setIsOpen] = useState(false);
    const navbarHeight = isOpen ? "420px" : "95px";
    return (
        <CartProvider>
            {/* <!-- Start Navbar section --> */}
            <nav className="navbar navbar-expand-lg bg-light" style={{ height: navbarHeight, transition: "height 0.3s ease" }}>
                <div className="container-fluid">
                    <img src="img/pizza logo.png" alt="" />
                    {/* <button data-mdb-collapse-init className="navbar-toggler" type="button" data-mdb-target="#navbarNavAltMarkup"
                        aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                        <i className="fas fa-bars"></i>
                    </button> */}
                    <NavbarToggler onClick={() => setIsOpen(!isOpen)} />
                    <Collapse className="navbar-collapse container-fluid" isOpen={isOpen} navbar id="navbarNavAltMarkup">
                        <div className="navbar-nav col-lg-8 col-md-12">
                            <NavLink className="nav-link navbar-home" aria-current="page" to="/">Home</NavLink>
                            <NavLink className="nav-link navbar-page" to="/about">About us</NavLink>
                            <NavLink className="nav-link navbar-page" to="/contact">Contact</NavLink>
                            <NavLink className="nav-link navbar-page" to="/sign">Sign in </NavLink>
                            <NavLink className="nav-link navbar-page" to="/register">Register</NavLink>
                            <NavLink className="nav-link navbar-page" to="/menu">Menu</NavLink>
                            <NavLink className="nav-link navbar-page" to="/cart"> <i class="fa-solid fa-cart-shopping" style={{ color: " #ff0000" }}></i> <span className='text-danger'>({totalItems})</span></NavLink>
                        </div>
                        <div className="col-lg-2 col-md-12">
                            <NavLink className="btn btn-dark navbar-button" to="/shop">Order online</NavLink>
                        </div>
                    </Collapse>
                </div>
            </nav>
            {/* <!-- End Navbar section --> */}
        </CartProvider>
    )
}

export default Navbar;        