import React from 'react'
import { NavLink } from 'react-router-dom';

function Content() {
  return (
    <div>
      {/* <!-- Start content section --> */}
      <div className="home-content">
        <div className="col-lg-5 animate__animated animate__flash">
          <h2>Authentic<br />Italian<br />pizza!</h2>
          <h4>Delicious Italian pizza<br /> straight from the oven <br /> with delivery to your door!</h4>
          <NavLink className="btn btn-outline-danger navbar-button" to="/shop">Order online</NavLink>
        </div>
      </div>
      {/* <!-- End content section --> */}
    </div>
  )
}

export default Content;