import React from 'react'
import Navbar from '../Navbar'
import { CartProvider } from 'react-use-cart'

function AllWelcome() {
    return (
        <CartProvider>
            <Navbar />
            <div className="welcome">
                <h2 className="animate__animated animate__rubberBand">Welcome To Your Account</h2>
                <i className="fa-solid fa-heart heart animate__animated animate__flip" ></i>
            </div>
        </CartProvider>
    )
}

export default AllWelcome