import { CartProvider, useCart } from "react-use-cart";
import Navbar from "../Navbar";
import Api from "../Menu/Api";
import {
  MDBBtn,
  MDBCard,
  MDBCardBody,
  MDBCardImage,
  MDBCardText,
  MDBCol,
  MDBContainer,
  MDBIcon,
  MDBInput,
  MDBRow,
  MDBTypography,
} from "mdb-react-ui-kit";
import React from "react";
import { NavLink } from "react-router-dom"; 
import Footer from "../Footer";
export default function CartMeal() {
  const {
    isEmpty,
    totalUniqueItems,
    items,
    totalItems,
    cartTotal,
    emptyCart,
    updateItemQuantity,
    removeItem,
  } = useCart();
  if (isEmpty) return <p className="empty-cart">Your meal is empty</p>;
  return (
   <div>
   <section className="h-100 h-custom" style={{ backgroundColor: "#ff0000 " }}>
      <Navbar/>
      <MDBContainer className="py-5 h-100">
        <MDBRow className="justify-content-center align-items-center h-100">
          <MDBCol size="12">
            <MDBCard className="card-registration card-registration-2" style={{ borderRadius: "15px" }}>
              <MDBCardBody className="p-0">
                <MDBRow className="g-0">
                  <MDBCol lg="8">
                    <div className="p-5">
                      <div className="d-flex justify-content-between align-items-center mb-5">
                        <MDBTypography tag="h1" className="fw-bold mb-0 text-danger wow bounceInLeft">
                          Meal Cart
                        </MDBTypography>
                        <MDBTypography className="mb-0 text-danger wow bounceInDown">
                          {totalUniqueItems} items
                        </MDBTypography>
                      </div>
                      {items.map((item) => (
                        <div className="wow pulse animated">
                          <hr className="my-4 text-danger" />
                          <MDBRow className="mb-4 d-flex justify-content-between align-items-center">
                            <MDBCol md="2" lg="2" xl="2" className="item-img">
                              <MDBCardImage
                                src={item.image}
                                fluid className="rounded-3" alt="pizza" />
                            </MDBCol>
                            <MDBCol md="3" lg="3" xl="3">
                              <MDBTypography tag="h6" className="text-muted">
                                {item.mealType}
                              </MDBTypography>
                              <MDBTypography tag="h6" className="text-black mb-0">
                                {item.name}
                              </MDBTypography>
                            </MDBCol>
                            <MDBCol md="3" lg="3" xl="3" sm="2" className="d-flex align-items-center  pizza-button-quantity">
                              <button className="btn btn-warning "
                                onClick={() => updateItemQuantity(item.id, (item.quantity ?? 0) - 1)}
                              >
                                -
                              </button>
                              <h3>{item.quantity}</h3>
                              <button className="btn btn-success"
                                onClick={() => updateItemQuantity(item.id, (item.quantity ?? 0) + 1)}
                              >
                                +
                              </button>
                            </MDBCol>
                            <MDBCol md="3" lg="2" xl="2" sm="9" className="text-end item-price-quantity">
                              <MDBTypography tag="h6" className="mb-0">
                                <h5>{Math.round(item.quantity * item.price)} $</h5>
                              </MDBTypography>
                            </MDBCol>
                            <MDBCol md="1" lg="1" xl="1" sm="1" className="text-end pizza-button-quantity">
                              <button className="btn btn-danger  " onClick={() => removeItem(item.id)}>&times;</button>
                            </MDBCol>
                          </MDBRow>
                        </div>
                      ))}
                      <hr className="my-4 text-danger" />

                      <div className="pt-5" data-wow-iteration="3">
                        <MDBTypography tag="h6" className="mb-0">
                          <NavLink tag="a" to="/shop" className="text-body text-danger wow lightSpeedIn">
                            <MDBIcon fas icon="long-arrow-alt-left me-2" /> Back
                            to menu
                          </NavLink>
                        </MDBTypography>
                      </div>
                    </div>
                  </MDBCol>
                  <MDBCol lg="4" className="bg-grey">
                    <div className="p-5">
                      <MDBTypography tag="h3" className="fw-bold mb-5 mt-2 pt-1 text-danger wow bounceInRight">
                        Summary
                      </MDBTypography>

                      <hr className="my-4" />

                      <div className="d-flex justify-content-between mb-4">
                        <MDBTypography tag="h5" className="text-uppercase wow lightSpeedIn">
                         <span className="text-danger"> {totalItems}</span> pizza
                        </MDBTypography>
                        <MDBTypography tag="h5" className="text-danger wow lightSpeedIn">{cartTotal}$</MDBTypography>
                      </div>

                      <MDBTypography tag="h5" className="text-uppercase mb-3 wow lightSpeedIn">
                        Shipping             =      20$
                      </MDBTypography>

                      <hr className="my-4" />

                      <div className="d-flex justify-content-between mb-5">
                        <MDBTypography tag="h5" className="text-uppercase wow lightSpeedIn">
                          Total price
                        </MDBTypography>
                        <MDBTypography tag="h5" className="text-danger wow lightSpeedIn">{cartTotal + 20} $</MDBTypography>
                      </div>

                      <MDBBtn onClick={emptyCart} color="danger wow shake" block size="lg">
                        Cancel Meal
                      </MDBBtn>
                    </div>
                  </MDBCol>
                </MDBRow>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
      <Footer/>
    </section>
    </div>
  );
}