import { CartProvider, useCart } from "react-use-cart";
import Navbar from "../Navbar";
import Api from "../Menu/Api";
import CartMeal from "../Cart/CartMeal";
function Page() {
    const { addItem } = useCart();

    const products = Api
    return (
        <div>
            <section className="shop_section layout_padding">
                <div className="container">
                    <div className="heading_container heading_center">
                        <h2>Meals</h2>
                    </div>
                    <div className="row">
                        {products.map((item) =>
                            <div className="col-sm-6 col-md-6 col-lg-4 wow pulse animated">
                                <div className="box">
                                    <div className="img-box">
                                        <img src={item.image} alt="" />
                                    </div>
                                    <div className="detail-box">
                                        <h5  id="text-menu-color"> {item.name}</h5>
                                    </div>
                                    <div className="detail-box">
                                        <h6>Price:<span> {item.price}$</span></h6>
                                        <h6>Rating:<span> {item.rating}<i class="fa-solid fa-star" style={{ color: "#FFD43B" }}></i></span></h6>
                                    </div>
                                    <div className="detail-box">
                                        <h5  id="text-menu-color"> {item.mealType}</h5>
                                    </div>
                                    <div className="detail-box">
                                        <button className=" form-control mt-2" id="text-menu-button" onClick={() => addItem(item)}>Add to meal  <i class="fa-solid fa-square-plus fa-lg" style={{ color: "#ffffff" }}></i></button>
                                    </div>
                                </div>
                            </div>
                        )}
                    </div>
                </div>
            </section>
        </div>
    );
}
function AllShop() {
    return (
        <CartProvider>
            <Navbar />
            <Page />
        </CartProvider>
    );
}
export default AllShop;